import * as React from "react";
import Layout from "../components/layout";
import SeoBusiness from "../components/seoBusiness";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import { graphql } from "gatsby";

const GalleryPage = ({ data }) => {
  const allImageGroup = data.allFile.group;
  const getImageFromGatsby = (image) => getImage(image);
  return (
    <Layout>
      <SeoBusiness title="Gallery" />
      {allImageGroup && allImageGroup.map((group, i) => (
        <>
          <section className="page-section cta" key={"header"+i} id={group.edges[0].node.relativeDirectory}>
            <div className="container">
                <div className="row">
                    <div className="col-xl-10 mx-auto">
                        <div className="cta-inner bg-faded text-center rounded">
                            <h2 className="section-heading">
                                <span className="section-heading-lower">{group.edges[0].node.relativeDirectory.replace(/-/g, " ")}</span>
                            </h2>
                        </div>
                        <div className="row">
                          {group.edges.map((edge) => (
                            <div
                              className="product-item col-md-6 mb-4"
                              key={edge.node.id}
                            >
                              <div className="left-0 text-center rounded">
                                <a href={edge.node.childImageSharp.gatsbyImageData.images.fallback.src}>
                                  <GatsbyImage
                                    className="product-item-img mx-auto d-flex rounded img-fluid mb-3 mb-lg-0 custom-box-shadow"
                                    image={getImageFromGatsby(edge.node)}
                                    quality={95}
                                    width={500}
                                    placeholder="blurred"
                                    formats={["auto", "webp", "avif"]}
                                    alt="sun"
                                  />
                                </a>
                              </div>
                            </div>
                          ))}
                        </div>
                    </div>
                </div>
            </div>
          </section>
        </>
      ))}
    </Layout>
  );
};
export default GalleryPage;

export const pageQuery = graphql`
  query GalleryPhotos {
    allFile(
      filter: {extension: {regex: "/(jpg)|(JPG)|(jpeg)|(png)/"}, sourceInstanceName: {eq: "gallery"}}
    ) {
      group(field: {relativeDirectory: SELECT}) {
        edges {
          node {
            id
            relativeDirectory
            name
            childImageSharp {
              gatsbyImageData
            }
          }
        }
      }
    }
  }
`;
